import {Component} from 'base-component';
// #------------------------------------# //
// #---- Component (super-iso-org-date) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	model: '=',
	org: '=',
	options: '=?',
	isDisabled: '=?',
	label: '@?',
	labelWeight: '@?',
	inputId: '@',
	name: '@',
	placeholder: '@?',
	dateFormat: '@',
	required: '=?',
	permissions: '=?',
	editMode: '='
};

// directive definition object overrides
const config = {
	transclude: false
};

/*@ngInject*/
function isoOrgDate() {

	class isoOrgDate extends Component {
		/*@ngInject*/
		controller($scope, ssDateByOrgHlp, moment) {
			// view model for tracking state
			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
				editMode: '',
				permissions: $scope.permissions || { view: true, edit: true, redacted: false }
			};

			const isoRegEx = /^\d{4}-[01]\d-[0-3]\dT[0-2]\d(:?[0-5]\d)?(:?[0-5]\d)?([:\.]?[0-5]\d{2})?([+-][0-2]\d:[0-5]\d|Z)$/g;

			// behavior that needs to be bound to the view
			const go = $scope.go = { // eslint-disable-line no-unused-vars
			};

			// Add a watch on 'edit-mode' so we can pass the updated value down to datepicker and invoke any internal watches there
			$scope.$watch('editMode', (nv, ov) => {
				vm.editMode = nv != null ?  nv === true : true;
			});

			// Add a watch on the value passed to the date picker so we can convert selected dates back to ISO format
			$scope.$watch('vm.orgRelativeIsoDate', (nv, ov) => {
				if(nv === ov) { return; }
				if(nv instanceof Date) {
					$scope.model = ssDateByOrgHlp.safeAsOfDateRelativeTo(moment(nv).startOf('day'), $scope.org, true);
				}
			});

			$scope.$watch('model', (nv, ov) => {
				if(nv) {
					if(typeof nv === 'string' && nv.match(isoRegEx)) {
						let orgRelativeDateMoment = ssDateByOrgHlp.utcIsoToRelativeMoment(nv, $scope.org);
						vm.orgRelativeIsoDate = ssDateByOrgHlp.safeAsOfDateRelativeTo(orgRelativeDateMoment.startOf('day'), $scope.org, true);
					} else {
						console.log('The date provided is not a string or does not match the ISO 8601 format'); // eslint-disable-line no-console
					}
				}
			});
		}
	}
	return new isoOrgDate(props, 'iso-org-date', config);
}

// #-- END Component (super-iso-org-date) --# //
// #------------------------------------# //

angular.module('super-suit-components').directive('superIsoOrgDate', isoOrgDate);
