/**
 * Multi Initiative Feature Detector Config Cat
 * 	The MIFD(CC) is a wrapper around the Config Cat Feature Flag service that
 * 	allows Frontline Central to manage flags more cleanly in UI Views. Currently
 * 	DIP and HRMS are two initiatives that are offering similar capabilities on
 * 	different time frames. This will let developers write code that is cleaner on
 * 	the UI so that they can test for features in ui-views like this:
 *
 *	ng-if="ssMultiInitiativeFeatureDetectorCCSvc.canCreateEmployee()"
 *		VS
 *	ng-if="ssFlagCCSvc.checkState('fc_di_disable_create_employee') || ssFlagCCSvc.checkState('fc_hrms_disable_create_employee')"
 *
 *	We cannot share flags across initiatives as they have different requirements
 *
 * **************************************************************************************
 * 	This class is of limited use outside of HRMS and DIP and is only wrapping other code
 * 	to help code readability. It is likely overkill.........
 * **************************************************************************************
 * @param {object} ssFlagCCSvc The SuperSuit's Config Cat Feature Flag checker service
 */

/*@ngInject*/
function ssMultiInitiativeFeatureDetectorCCSvc(ssFlagCCSvc) {
	// #-------------------------------------------------------# //
	// #---- Service (ssMultiInitiativeFeatureDetectorCCSvc) ----# //

	// External service API
	const MIFD_API = {
		canCreateEmployee,
		canDeleteEmployee,
		isGlobalDirectoryEnabled,
		isCredentialsFormGroupEnabled,
		isDataImporterEnabled,
		isEmergencyContactsTabEnabled,
		isPersonalReadonly,
		isEmploymentReadonly,
		isConfigurationListManagementReadonly,
		isDataIntegrationsEnabled,
		isHRMSEnabled,
		isHRMSWithERPEnabled,
		isEscapeIntegrationEnabled,
		isGlobalFcSharedEmployeeEnabled,
		isFCSystemOfRecord,
		isGlobalOrSharedProfile,
		hasStateEducatorIdonPersonal,
		hasStateEducatorIdonEmployment,
		showEmploymentGroups,
		showTenureDate,
		resolveAll
	};

	return MIFD_API;

	function mapFFlags(flags) {
		let keys = Object.keys(flags || {});
		let checks = keys.map((flag) => {
			return typeof MIFD_API[flag] === 'function' ? MIFD_API[flag]() : ssFlagCCSvc.checkState(flag);
		});
		return {
			keys,
			checks
		};
	}

	function resolveAll(flags) {
		let flagMap = mapFFlags(flags);
		return ssFlagCCSvc.ready.then(() => {
			return Promise.all(flagMap.checks);
		}).then((res) => {
			res.forEach((r, i) =>{
				flags[flagMap.keys[i]] = r;
			});
		});
	}

	/**
	 * A top level check to be used to see if only one of the top level flags are set. If multiple are set we need to throw.
	 */
	function throwWhenFlagsSetIncorrectly() {
		return Promise.all([
			ssFlagCCSvc.checkState('global_data_integration'),
			ssFlagCCSvc.checkState('global_hrms'),
			ssFlagCCSvc.checkState('hrms_with_erp')
		]).then(result => {
			const [dataIntegration, hrms, hrmsWithErp] = result;
			const ret = dataIntegration && (hrms || hrmsWithErp);
			if (ret) {
				throw new Error('Data integrations AND HRMS are enabled at the same time. This is an error state please verify that the feature flags are set correctly');
			}
			return ret;
		});
	}

	/* ******** BASE CHECKS ******** */

	/**
	 * Tests to see if the Data Integrations flag is enabled.
	 * Note: HRMS should not be enabled if Data Integrations is enabled.
	 * @returns {bool} Boolean describing if the data integrations flag is set or not.
	 */
	function isDataIntegrationsEnabled() {
		return throwWhenFlagsSetIncorrectly()
			.then(() => ssFlagCCSvc.checkState('global_data_integration'));
	}

	/**
	 * Tests to see if the HRMS option of Frontline Central is enabled
	 * Note: DataIntegrations should not be enabled if hrms is enabled.
	 * @returns {bool} Boolean describing if the HRMS flag is set or not.
	 */
	function isHRMSEnabled() {
		return throwWhenFlagsSetIncorrectly()
			.then(() => ssFlagCCSvc.checkState('global_hrms'));
	}

	/**
	 * Tests to see if the HRMS with ERP option of Frontline Central is enabled
	 * Note: DataIntegrations should not be enabled if hrms is enabled.
	 * @returns {bool} Boolean describing if the HRMS-with-erp flag is set or not.
	 */
	function isHRMSWithERPEnabled() {
		return throwWhenFlagsSetIncorrectly()
			.then(() => ssFlagCCSvc.checkState('hrms_with_erp'));
	}

	function isEscapeIntegrationEnabled() {
		return ssFlagCCSvc.checkState('global_fc_escape_integration');
	}

	function isGlobalFcSharedEmployeeEnabled() {
		return ssFlagCCSvc.checkState('global_fc_shared_employee');
	}

	function isFCSystemOfRecord() {
		return Promise.all([
			isHRMSEnabled(),
			isEscapeIntegrationEnabled()
		]).then(result => {
			const [hrms, escapeIntegration] = result;
			return !(hrms || escapeIntegration);
		});
	}

	/* ******** END BASE CHECKS ******** */

	/* ******** ACTION CHECKS ******** */

	/**
	 * Can the user Create an Employee within Frontline Central
	 * REMARKS: Does not control ability within Global Directory or Global Profile
	 * @returns {bool} Boolean describing the ability to take the action
	 */
	function canCreateEmployee() {
		return Promise.all([
			isDataIntegrationsEnabled(),
			isHRMSEnabled()
		]).then(result => {
			const [dataIntegration, hrms] = result;
			return !(dataIntegration || hrms);
		});
	}

	/* ******** END ACTION CHECKS ******** */

	/* ******** EDIT CHECKS ******** */

	/**
	 * Is the feature to make the FC personal tab readonly enabled?
	 * 	REMARKS: this is for the FC version of the personal tab, not the global profile personal tab
	 * @returns {bool} Boolean describing the ability to take the action
	 */
	function isPersonalReadonly() {
		return Promise.all([
			isDataIntegrationsEnabled(),
			isHRMSEnabled()
		]).then(result => {
			const [dataIntegration, hrms] = result;
			return dataIntegration || hrms;
		});
	}

	/**
	 * Is the feature to make the FC employee tab readonly enabled?
	 * @returns {bool} Boolean describing the ability to take the action
	 */
	function isEmploymentReadonly() {
		return isHRMSEnabled();
	}

	/**
	 * Is the feature to enable the readonly Configuration Lists enabled?
	 * 	(HRMS will not use FC lists)
	 * @returns {bool} Boolean describing the ability to edit items in List Configuration
	 */
	function isConfigurationListManagementReadonly() {
		return isHRMSEnabled();
	}

	/* ******** END EDIT CHECKS ******** */

	/* ******** FEATURE CHECKS ******** */

	/**
	 * Is the feature to show the Global Directory enabled?
	 * @returns {bool} Boolean describing the ability to view the global directory
	 */
	function isGlobalDirectoryEnabled() {
		return isDataIntegrationsEnabled(); // DIP orgs get global directory
	}

	/**
	 * Is the StateEducatorId on the personal Tab? (HRMS)
	 * @returns {bool} Boolean describing the ability to view the state educator id field on the Personal Tab in the Employee Record
	 */
	function hasStateEducatorIdonPersonal() {
		return Promise.all([
			isHRMSEnabled(),
			isDataIntegrationsEnabled(),
		]).then(result => {
			const [hrms, dataIntegration] = result;
			let ret = hrms;
			if (dataIntegration) {
				// Always the case, so eject
				return false;
			}

			if (hrms || (!hrms && !dataIntegration)) {
				// if they're both disabled we show on personal
				// if only hrms is enabled we show on personal
				ret = true;
			}
			return ret;
		});
	}

	/**
	 * Is the StateEducator ID on the Employment Tab (DIP)
	 * @returns {bool} Boolean describing the ability to view the state educator id field on the Employment Tab in the Employee Record
	 */
	function hasStateEducatorIdonEmployment() {
		// its only on the employment tab if DIP is on
		return isDataIntegrationsEnabled();
	}

	/**
	 * Is the Employee Record Emergency Contacts Tab enabled
	 * @returns {bool} Boolean describing the ability to view the Emergency Contacts tab in the Employee Record
	 */
	function isEmergencyContactsTabEnabled() {
		return Promise.all([
			isHRMSEnabled(),
			isDataIntegrationsEnabled()
		]).then(result => {
			const [hrms, dataIntegration] = result;
			let ret = !hrms; // if HRMS we don't get emergency contacts

			if (!hrms && !dataIntegration) {
				ret = true; // if both are disabled, then then you get it
			}

			return ret;
		});
	}

	/**
	 * Is the current organization able to delete an employee through Frontline Central
	 * @returns {bool} Boolean describing the ability to delete an employee through Frontline Central
	 */
	function canDeleteEmployee() {
		// if you have HRMS enabled you can't delete an employee
		return Promise.all([
			isHRMSEnabled(),
			isDataIntegrationsEnabled()
		]).then(result => {
			const [hrms, dataIntegration] = result;
			return !(hrms || dataIntegration);
		});
	}

	/**
	 * Do I show the employment groups?
	 * @returns {bool} Boolean describing the ability to show employment groups
	 */
	function showEmploymentGroups() {
		return isHRMSEnabled().then(enabled => !enabled);
	}

	/**
	 * Do I show the Tenure Date for an employee
	 * @returns {bool} Boolean describing the ability to show tenure date
	 */
	function showTenureDate() {
		return isHRMSEnabled().then(enabled => !enabled);
	}

	/**
	 * is the Data Importer enabled
	 * @returns {bool} Boolean describing whether the Data Importer is enabled
	 */
	function isDataImporterEnabled () {
		return Promise.all([
			ssFlagCCSvc.checkState('fc_di_enable_data_importer'),
			isHRMSEnabled()
		]).then(result => {
			const [dataImporter, hrms] = result;
			return hrms ? false : dataImporter;
		});
	}

	/**
	 * is the Credentials Group on forms enabled
	 * @returns {bool} Boolean describing whether the Credentials component within forms is enabled
	 */
	function isCredentialsFormGroupEnabled () {
		return Promise.all([
			ssFlagCCSvc.checkState('fc_enable_credentials_group'),
			isHRMSWithERPEnabled()
		]).then(result => {
			const [credentialsGroup, hrmsWithERP] = result;
			return hrmsWithERP ? false : credentialsGroup;
		});
	}


	/**
	 * Is DIP or Global Shared Employee enabled
	 * Determines whether we hide "above the line" Employee Directory
	 * @returns {bool} Boolean describing whether the Credentials component within forms is enabled
	 */
	function isGlobalOrSharedProfile () {
		return Promise.all([
			ssFlagCCSvc.checkState('global_fc_shared_employee'),
			isDataIntegrationsEnabled()
		]).then(result => {
			const [sharedEmployee, isDataIntegrationsEnabled] = result;
			return sharedEmployee || isDataIntegrationsEnabled;
		});
	}



	/* ******** FEATURE CHECKS ******** */

	// #-- END Service (ssMultiInitiativeFeatureDetectorCCSvc) --# //
	// #-------------------------------------------------------# //
}

angular.module('super-suit-services').factory('ssMultiInitiativeFeatureDetectorCCSvc', ssMultiInitiativeFeatureDetectorCCSvc);
