import {
	Component
} from 'base-component';
// #---------------------------------------------# //
// #---- Component (super-single-filter-list) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	selected: '=',
	label: '@?',
	labelWeight: '@?',
	isDisabled: '=?',
	includeNoneOption: '=?',
	editMode: '=',
	items: '=',
	model: '=',
	databindName: '@?',
	databindValue: '@?',
	required: '@?'
};

// directive definition object overrides
const config = {
	transclude: false
};

/*@ngInject*/
function singleFilterList(ssCommonControlHlp) {

	class SingleFilterList extends Component {
		/*@ngInject*/
		controller($scope, $attrs, $log) {
			$scope.labelWeight = ssCommonControlHlp.labelWeight($scope.labelWeight, $scope.label);
			$scope.items == null ? $scope.items = [] : $scope.items;

			// Ensure the defaults
			$scope.databindName = $scope.databindName == null ? '_name' : $scope.databindName;
			$scope.databindValue = $scope.databindValue == null ? 'value' : $scope.databindValue;

			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
				label: $scope.label,
				selected: $scope.model ? $scope.items.find(v => v[$scope.databindValue] === $scope.model) : null
			};

			let items = $scope.items.map(v => {
				// Fluid select binds to _name and value properties.  Make sure they're there.
				if (!v.hasOwnProperty('_name')) {
					v._name = v[$scope.databindName];
				}
				v.value = v[$scope.databindValue];
				return v;
			});
			// console.log('mapped items', items);
			$scope.items = items;

			if ($scope.includeNoneOption) {
				$scope.items.splice(0, 0, {
					_name: 'None',
					value: null,
					name: 'None'
				});
			}

			const go = $scope.go = { // eslint-disable-line no-unused-vars
				click($event) {
					$scope.model = $event[$scope.databindValue];
				},
				getSelectedText() {
					let r = 'None';
					if ($scope.model != null && $scope.items.length) {
						let item = $scope.items.find(v => v[$scope.databindValue] === $scope.model);
						if (item != null) {
							r = item[$scope.databindName];
						}
					}
					return r;
				}
			};

			$scope.$watchCollection('items', (newVal, oldVal) => {
				if (newVal === oldVal) { return; }

				let list = newVal.map(v => {

					if (!v.hasOwnProperty('_name')) {
						v._name = v[$scope.databindName];
					}
					v.value = v[$scope.databindValue];
					return v;
				});
				$scope.items = list;
			});
		}

		link(scope, elem, attrs, ngModel) {
			attrs.$observe('label', val => {
				scope.vm.label = val;
			});
		}
	}

	return new SingleFilterList(props, 'single-filter-list', config);
}

// #-- END Component (super-single-filter-list) --# //
// #---------------------------------------------# //

angular.module('super-suit-components').directive('superSingleFilterList', singleFilterList);
