import {
	Component
} from 'base-component';
// #------------------------------------# //
// #---- Component (super-lifeline) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	helpId: '@'
};

// directive definition object overrides
const config = {
	// transclude: true
};

/**
 * Lifeline is a basic tool to help push users to the Learning Center for help on a feature of the site.
 *
 */
/*@ngInject*/
function lifeline() {

	class Lifeline extends Component {
		/*@ngInject*/
		controller($scope, $window, $document) {
			// view model for tracking state
			const baseUrl = 'https://central-help.frontlineeducation.com/hc/en-us/articles';

			/**
			 * A tiny helper to build out help objects for the lifeline feature
			 * @param {int} id The internal id of the list of helper links
			 * @param {string} description  A description of the help link
			 * @param {string} url The id of the learning center article
			 */
			const helpHelper = (id, productCode, description, helpId) => {
				return {
					id: id,
					product: productCode,
					description: description,
					learningCenterId: helpId
				};
			};

			// The internal list of learning center article links.
			// TODO: This should be out on some database so that it can be managed externally
			const helpMapper = [
				helpHelper(1, 'EC', 'Edit an Employee Record', '115003574508'),
				helpHelper(2, 'EC', 'Creating a New Employee Record', '115003458107'),
				helpHelper(3, 'EC', 'Importing using Recruiting & Hiring\'s Hiring Wizard [Video]', '360001839188'),
				helpHelper(4, 'EC', 'Completing an Employee Record', '115003458127'),
				helpHelper(5, 'EC', 'Changing an Employee\'s Employment Status', '360001461607'),
			];

			const vm = $scope.vm = {
				helpMap: helpMapper.find(v => v.id === parseInt($scope.helpId, 10))
			};

			// $scope.$on('$stateChangeSuccess', function (event, args) {
			// 	console.log('$stateChangeSuccess');
			// });

			// behavior that needs to be bound to the view
			const go = $scope.go = { // eslint-disable-line no-unused-vars
				openHelpUrl() {
					$window.open(`${baseUrl}/${vm.helpMap.learningCenterId}`, '_blank');
				}
			};
		}
	}

	return new Lifeline(props, 'lifeline', config);
}

// #-- END Component (super-lifeline) --# //
// #------------------------------------# //

angular.module('super-suit-components').directive('superLifeline', lifeline);
