(function(e){try{e=angular.module("super-templates")}catch(t){e=angular.module("super-templates",[])}e.run(["$templateCache",function(c){c.put('components/super-iso-org-date/iso-org-date.html',`<div class="super-iso-org-date"><super-input-datepicker model="vm.orgRelativeIsoDate" label="{{label}}" label-weight="{{labelWeight}}" options="options" input-id="{{inputId}}" name="{{name}}" placeholder="{{placeholder}}" date-format="{{dateFormat}}" edit-mode="vm.editMode" is-disabled="isDisabled" required="required" permissions="vm.permissions"></super-input-datepicker></div>`);c.put('components/super-lifeline/lifeline.html',`<div class="super-lifeline"><super-button smp ss-icon="question-circle" ng-click="go.openHelpUrl()" data-contextual-help="{{vm.helpMap.learningCenterId}}" aria-label="Help documentation for: {{vm.helpMap.description}}" title="Get help on {{vm.helpMap.description}}"></super-button></div>`);c.put('components/super-release-notes/release-notes.html',`<div class="super-release-notes" ng-show="vm.releaseNotes.length > 0"><h2 class="release-notes-header"><i ng-hide="true" ss-icon="li-lightbulb"></i> What's New</h2><div class="navigation"><button ss-icon-after="angle-left" class="release-note-button left" type="button" ng-click="go.prev()" ng-if="vm.releaseNotes.length > 1"></button> <span>{{ go.formatReleaseDate(vm.releaseNotes[vm.currentIndex]) }}</span> <button ss-icon-after="angle-right" class="release-note-button right" type="button" ng-click="go.next()" ng-if="vm.releaseNotes.length > 1"></button></div><p ng-if="vm.useExternal">{{vm.jumpToExternalUrl}}</p><!-- Render Notes as strings --> <span class="release-note" data-index="{{vm.currentIndex}}" ng-if="!vm.releaseNotes[vm.currentIndex].renderNoteAsHtml">{{ vm.releaseNotes[vm.currentIndex].note }} </span><!-- Render Notes as HTML --> <span class="release-note" data-index="{{vm.currentIndex}}" ng-if="vm.releaseNotes[vm.currentIndex].renderNoteAsHtml" ss-bind-html="vm.releaseNotes[vm.currentIndex].note"></span><div class="learn-more" ng-if="go.hasLearningCenterArticle(vm.releaseNotes[vm.currentIndex])"><!-- <super-button link ng-click="go.openLearningCenter(vm.releaseNotes[vm.currentIndex])" title="Learn More" ss-icon-after="external-link" label="Learn More"></super-button> --> <a ng-click="go.openLearningCenter(vm.releaseNotes[vm.currentIndex])" title="Learn More" ss-icon-after="external-link">Learn More</a></div></div>`);c.put('components/super-single-filter-list/single-filter-list.html',`<div class="super-single-filter-list" ng-class="{'is-disabled': isDisabled}"><label ng-if="label" for="{{vm.controlId}}" class="main-label" ng-class="{'{{labelWeight}}':labelWeight, 'required': required}" ng-bind="vm.label"></label><div ng-show="editMode"><fluid-select items="items" databind-name="databindName" databind-value="databindValue" selected-value="model" on:update-selected-value="go.click($event)"></fluid-select></div><div ng-hide="editMode" ng-bind="go.getSelectedText()"></div></div>`);}])}())