import {
	Component
} from 'base-component';
import {
} from 'change-case';
// #-----------------------------------------# //
// #---- Component (super-release-notes) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	notes: '=',
	isAdmin: '<'
};

// directive definition object overrides
const config = {
	// transclude: true
};

/*@ngInject*/
function releaseNotes(moment) {

	class ReleaseNotes extends Component {
		/*@ngInject*/
		controller($scope, $window) {
			const baseUrl = 'https://central-help.frontlineeducation.com/hc/en-us/articles'; // NOSONAR

			// view model for tracking state
			const vm = $scope.vm = { // eslint-disable-line no-unused-vars
				currentIndex: 0,
				releaseNotes: $scope.notes,
			};

			//Filter down to the notes the user is allowed to see
			if ($scope.isAdmin) {
				vm.releaseNotes = $scope.notes.filter(v => v.isAdmin === true);
			}

			// behavior that needs to be bound to the view
			const go = $scope.go = { // eslint-disable-line no-unused-vars
				next() {
					let idx = vm.currentIndex;
					// console.log('Next note');
					if (idx === vm.releaseNotes.length - 1) {
						// console.log('Setting index back to beginning');
						idx = 0;
					} else {
						// console.log('Setting index to next note');
						idx += 1;
					}
					// console.log(idx);
					vm.currentIndex = idx;
				},
				prev() {
					let idx = vm.currentIndex;
					// console.log('Next note');
					if (idx === 0) {
						// console.log('Setting index back to end');
						idx = vm.releaseNotes.length - 1;
					} else {
						// console.log('Setting index to next note');
						idx -= 1;
					}
					vm.currentIndex = idx;
				},
				formatReleaseDate(note) {
					let d = moment(note.releaseDate);

					if (d.isValid()) {
						return moment(note.releaseDate).format('LL');
					} else {
						return 'Earlier this year';
					}
				},
				hasLearningCenterArticle(note) {

					return note.hasOwnProperty('learningCenterId') && !(note.learningCenterId == null || note.learningCenterId === '');
				},
				openLearningCenter(note) {
					if (note.hasOwnProperty('learningCenterId') && !(note.learningCenterId == null || note.learningCenterId === '')) {
						$window.open(`${baseUrl}/${note.learningCenterId}`, '_blank');
					}
				}
			};
		}
	}

	return new ReleaseNotes(props, 'release-notes', config);
}

// #-- END Component (super-release-notes) --# //
// #-----------------------------------------# //

angular.module('super-suit-components').directive('superReleaseNotes', releaseNotes);
